<template>
  <div class="nick-name">
    <nav-head title="昵称">
      <span slot="right" class="question-icon nav-right" @click="saveEdit" >保存</span>
    </nav-head>
    <div class="wrap">
      <input type="text" v-model="nickName" maxlength="10" placeholder="当前的昵称"/>
      <p>注意：昵称长度不超过10个字！</p>
    </div>
  </div>
</template>
<script>//
import NavHead from '@/components/NavHead.vue'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { EDIT_NICKNAME } from '@/vuex/Mine'

export default {
  name: 'nickName',
  data () {
    return {
      nickName: this.$route.query.name
    }
  },
  methods: {
    saveEdit () {
      console.log(this.nickName)
      if (this.nickName) {
        this.editNickname({
          user_nickname: this.nickName
        })
      } else {
        Toast('昵称不能为空！')
      }
    },
    ...mapActions({
      editNickname: EDIT_NICKNAME
    })
  },
  components: {
    NavHead,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  .nick-name{
    text-align: left;
    color: #999;
    font-size: 14px;
    min-height: calc(100vh - 0px);
    position: absolute;
    width: 100%;
    background-color: $background-light-color;
    > div.wrap {
      padding: 0 25px;
      margin-top: 20px;
      > input {
        border: none;
        outline: none;
        width: 100%;
        font-size: 16px;
        height: 56px;
        line-height: 56px;
        margin-bottom: 10px;
        background: none;
        border-bottom: 1px solid $border-color;
        border-radius: 0;
        color: $font-color;
      }
      > p {
        @if $IF365 { color: #CCC; }
        @else { color: #999; }
      }
    }
  }
</style>
